import axiosInstance from "@/api/axios/axiosInstance";
import { toast } from "sonner";

// Types
export interface Campaign {
  id: string;
  orgId: string;
  name: string;
  status: "scheduled" | "in-progress" | "ended";
  phoneNumberId: string;
  customers: Customer[];
  assistantId?: string;
  workflowId?: string;
  schedulePlan?: SchedulePlan;
  calls: Record<string, CampaignCall>;
  callsCounterScheduled: number;
  callsCounterQueued: number;
  callsCounterInProgress: number;
  callsCounterEndedVoicemail: number;
  callsCounterEnded: number;
  endedReason?: string;
  createdAt: string;
  updatedAt: string;
}

export interface Customer {
  number: string;
  name?: string;
  email?: string;
  externalId?: string;
  sipUri?: string;
  numberE164CheckEnabled?: boolean;
  extension?: string;
  assistantOverrides?: any;
}

export interface SchedulePlan {
  earliestAt?: string;
  latestAt?: string;
}

export interface CampaignCall {
  id: string;
  status: string;
  customer: Customer;
  startedAt?: string;
  endedAt?: string;
  duration?: number;
  cost?: number;
  transcript?: string;
  recording?: string;
  endedReason?: string;
  analysis?: {
    summary?: string;
    successEvaluation?: string;
    [key: string]: any;
  };
}

export interface CreateCampaignRequest {
  name: string;
  phoneNumberId: string;
  customers: Customer[];
  assistantId?: string;
  workflowId?: string;
  schedulePlan?: SchedulePlan;
  autoLaunch?: boolean;
}

export interface UpdateCampaignRequest {
  name?: string;
  phoneNumberId?: string;
  customers?: Customer[];
  assistantId?: string;
  workflowId?: string;
  schedulePlan?: SchedulePlan;
  status?: "scheduled" | "in-progress" | "ended";
}

// Get all campaigns
export async function getAllCampaigns() {
  try {
    const res = await axiosInstance.get("/api/outboundcall/campaigns");
    return res.data;
  } catch (error: any) {
    console.error("Error fetching campaigns:", error);

    // Don't show toast error for expected scenarios:
    // - 404: No campaigns found (normal for new users)
    // - 204: Empty results
    // - 402: Payment required (normal for users without credits)
    const expectedStatuses = [404, 204, 402];
    if (!expectedStatuses.includes(error?.response?.status)) {
      const message = error.response?.data?.message || "Failed to fetch campaigns";
      toast.error(message);
    }

    // Return empty result instead of throwing for expected scenarios
    if (expectedStatuses.includes(error?.response?.status)) {
      return { data: [], success: true, message: "No campaigns found" };
    }

    throw error;
  }
}

