// Launch campaign
export async function launchCampaign(id: string) {
  try {
    console.log(`Launching campaign: ${id}`);
    const res = await axiosInstance.post(`/api/outboundcall/campaigns/${id}/launch`);
    console.log("Launch campaign response:", res.data);
    toast.success("Campaign launched successfully");
    return res.data;
  } catch (error: any) {
    console.error("Error launching campaign:", {
      campaignId: id,
      error: error.response?.data || error.message,
      status: error.response?.status,
      statusText: error.response?.statusText
    });

    const message = error.response?.data?.message || error.response?.data?.error || "Failed to launch campaign";
    const details = error.response?.data?.details;

    if (details) {
      console.error("Launch error details:", details);
    }

    toast.error(message);
    throw error;
  }
}

