// Parse CSV file for customers
export function parseCSVFile(file: File): Promise<Customer[]> {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.onload = (e) => {
      const csv = e.target?.result as string;
      const lines = csv.split('\n');
      const headers = lines[0].split(',').map(h => h.trim().toLowerCase());
      
      if (!headers.includes('number')) {
        reject(new Error('CSV must contain a "number" column'));
        return;
      }

      const customers: Customer[] = [];
      
      for (let i = 1; i < lines.length; i++) {
        const line = lines[i].trim();
        if (!line) continue;
        
        const values = line.split(',').map(v => v.trim());
        const customer: Customer = { number: '' };
        
        headers.forEach((header, index) => {
          const value = values[index] || '';
          switch (header) {
            case 'number':
              // Normalize phone number to E.164 format
              let normalizedNumber = value.replace(/\D/g, ''); // Remove all non-digits
              if (normalizedNumber && !normalizedNumber.startsWith('1') && normalizedNumber.length === 10) {
                // Assume US number if 10 digits without country code
                normalizedNumber = '1' + normalizedNumber;
              }
              if (normalizedNumber && !normalizedNumber.startsWith('+')) {
                normalizedNumber = '+' + normalizedNumber;
              }
              customer.number = normalizedNumber;
              break;
            case 'name':
              customer.name = value;
              break;
            case 'email':
              customer.email = value;
              break;
            case 'externalid':
            case 'external_id':
              customer.externalId = value;
              break;
            case 'sipuri':
            case 'sip_uri':
              customer.sipUri = value;
              break;
            case 'extension':
              customer.extension = value;
              break;
          }
        });
        
        if (customer.number) {
          customers.push(customer);
        }
      }
      
      resolve(customers);
    };
    
    reader.onerror = () => reject(new Error('Failed to read CSV file'));
    reader.readAsText(file);
  });
}

// Download CSV template
export function downloadCSVTemplate() {
  const csvContent = "number,name,email,external_id\n+14151234567,John Doe,john@example.com,customer_001\n+14159876543,Jane Smith,jane@example.com,customer_002";
  const blob = new Blob([csvContent], { type: 'text/csv' });
  const url = window.URL.createObjectURL(blob);
  const a = document.createElement('a');
  a.href = url;
  a.download = 'campaign_template.csv';
  document.body.appendChild(a);
  a.click();
  document.body.removeChild(a);
  window.URL.revokeObjectURL(url);
}
